.header {
  &-section {
    position: relative;
    z-index: 1;
  }

  &-content {
    position: absolute;
    width: 100%;
    padding: 46px 0;

    @media(max-height: 700px) {
      padding: 30px 0;
    }

    @media(max-width: 1024px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 28px 0;
    }
  }

  &-logo {
    line-height: 0;
  }

  &-burger {
    display: none;

    @media(max-width: 768px) {
      display: block;
    }
  }

  &-menu {
    display: none;

    &.active {
      display: block;
      position: fixed;
      left: 0;
      top: 0;
      background-color: #10263F;
      width: 100%;
      height: 100%;
    }

    &-section {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: 28px;
      padding-bottom: 33px;
      border-bottom: 1px solid #264669;
      margin-bottom: 38px;
    }

    &-contact {
      span {
        display: block;
        font-style: italic;
        font-weight: 800;
        font-size: 24px;
        text-transform: uppercase;
        color: #FFF500;
        margin-bottom: 29px;
      }

      a {
        display: block;
        font-weight: 700;
        font-size: 15px;
        color: #fff;
        margin-bottom: 10px;
      }
    }
  }
}